import { precompileTemplate } from "@ember/template-compilation";
export default precompileTemplate("<LfGetOutletState as |outletState|>\r\n  {{~!~}}\r\n  <LiquidBind\r\n    @value={{(lf-lock-model outletState this.outletName)}}\r\n    @containerId={{@containerId}}\r\n    @versionEquality={{this.versionEquality}}\r\n    @matchContext={{(hash outletName=this.outletName helperName=\"liquid-outlet\")}}\r\n    @class={{@class}}\r\n    @use={{@use}}\r\n    @rules={{@rules}}\r\n    @containerless={{@containerless}}\r\n    @containerElement={{@containerElement}}\r\n    @growDuration={{@growDuration}}\r\n    @growPixelsPerSecond={{@growPixelsPerSecond}}\r\n    @growEasing={{@growEasing}}\r\n    @shrinkDelay={{@shrinkDelay}}\r\n    @growDelay={{@growDelay}}\r\n    @enableGrowth={{@enableGrowth}} as |version|\r\n  >\r\n  {{~!~}}\r\n    {{~#-with-dynamic-vars outletState=version~}}\r\n      {{outlet}}\r\n    {{~/-with-dynamic-vars~}}\r\n  {{~!~}}\r\n  </LiquidBind>\r\n  {{~!~}}\r\n</LfGetOutletState>")